
@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap');


body {
  background-color: black;
  font-family: 'DM Serif Display', serif;
}

.box {
  background-color: rgb(0, 43, 67);
  margin: 0 auto;
  text-align: center;
  width: 70%;
  max-width: 600px;
  padding: 20px 0px 50px 0px;
  margin-top: 60px;
  border-radius: 15px;
  min-width: 450px;
}

h3 {
  color: rgb(55, 227, 201);
}
form {
  color: rgb(55, 227, 201);
}


.error {
   
  background: rgb(133, 133, 133);
  font-size: 16px;
  border: solid 0.5px;
  width: 80%;
  margin: auto;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}



form button {
  background-color: rgb(106, 205, 172);
  border: rgb(5, 132, 60) solid 2px;
  border-radius: 25px;
  padding: 0px 12px 0px 12px;
  margin: 10px 0px 25px 0px;
  color: white;
}

form button:hover {
  background-color: rgb(43, 251, 182);  

}

table {
  align-content: center;
  margin: auto;
  width: 70%;
  max-width: 500px;  
}

tr {
  height: 45px;
}

td {
  min-width: 120px;
}

.numberstitle {
  color: rgb(245, 245, 245);
  font-size: 25px;
}

.numberslist {
  list-style: "â†’ ";
  color: rgb(45, 182, 228);
  padding-top: 3px;
  font-size: 16px;
}

.numberslist i {
  font-style: italic;
}

.numberslist button {
  background-color: rgb(225, 73, 73);
  color: white;
  border-radius: 25px;
  border: rgb(94, 1, 1) solid 2px;  
  font-size: 15px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode';
  padding: 0px 8px 0px 8px;
}

.numberslist button:hover {
  background-color: rgb(187, 21, 21);
}

.maintitle {
  color: rgb(245, 245, 245);
  font-size: 32px;
}

.maintitle:hover {
  /* color: rgb(108, 106, 106); */
  font-size: 32px;
  /* background-color: rgb(59, 61, 174); */
  box-shadow: 5px 0px 10px inset #1998b8;
}

.filterbox {
 color: rgb(249, 249, 249);
 margin-bottom: 25px;
}

.msgfilter{
  color: rgb(174, 35, 35);
}

.msgfilter:hover{
  color: rgb(240, 48, 0);
}